import { DynamicsPrivacyLevels } from './dynamics-privacy-levels';

/**
 * Wrapper class that attaches Dynamics privacy classification info to an object.
 */
export type DynamicsPrivacyLog<T> = {
    /**
     * Generic data value that is going to be logged
     */
    data: T;

    /**
     * Privacy classification level from DACS
     */
    classificationLevel: DynamicsPrivacyLevels;

    /**
     * Marks whether data contains customer info
     */
    isCustomerData: boolean;

    /**
     * Marks whether data contains restricted info
     */
    isRestrictedData: boolean;

    /**
     * Marks whether data contains personal info
     */
    isPersonalData: boolean;

    /**
     * Discriminates this object as a DynamicsPrivacyLog object
     */
    __isDynamicsPrivacy: boolean;
};
